.appBar {
  position: sticky;
  z-index: 100;
  top: 0;
  left: 0;

  display: flex;
  justify-content: space-between;
  height: 50px;
  color: aliceblue;
  background-color: #080808;
  padding-top: 12px;
  padding-bottom: 12px;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
